import React from 'react';
import CircularProgress from 'material-ui/CircularProgress';
import './style/text.css'

const Contact = ({state}) => {

    return (
        <div className="widthLimit">
            {state.infoLoading?<div><h3>Dynamically Loading</h3>
                <CircularProgress size={250} thickness={5} />            
                                <div><h3>From MongoDB</h3></div></div>
                            :
           
                <div className="contact">
                <span><h2>Email:</h2><p><a href={`mailto:${state.information.email}`} target="_blank">{state.information.email}</a></p></span>
                <span><h2>Phone:</h2><p>{state.information.phone}</p></span>
                <span><h2>Github:</h2><p><a href={state.information.github} target="_blank">{state.information.github}</a></p></span>
                </div>
           
            }
        </div>
    )

}

export default Contact;


