import React from 'react';
import CircularProgress from 'material-ui/CircularProgress';
import './style/projects.css';

const Projects = ({state}) => {
    return (
        
        <div className="container">
            {state.projLoading?<div><h3>Dynamically Loading</h3>
                <CircularProgress size={250} thickness={5} />
                                <h3>Projects From MongoDB</h3>
                                </div>
                            :
                state.projects.map((item, i) =>
                <div className="project" key={i}>
                    <span className="front">
                        <img src={item.img} alt={item.name} height="350px" width="350px"/>
                    </span>
                    <span className="back">
                        <a href={item.url} target="_blank"><h1>{item.name}</h1></a>
                        <p>{item.description}</p><hr/><strong>{item.langs.join(' ◦ ')}</strong>
                        <a href={item.git} target="_blank"><h3>Github</h3></a>
                    </span>
                </div>
            )}
        </div>
    )
}

export default Projects;


