import React from 'react';
import CircularProgress from 'material-ui/CircularProgress'
import './style/text.css'

const About = ({state}) => {
    return (
        <div className="widthLimit">
        {state.infoLoading?<div><h3>Dynamically Loading</h3>
            <CircularProgress size={250} thickness={5} />            
                            <div><h3>From MongoDB</h3></div></div>
                            :
                            <div>
        <img src={state.information.img} height="200px" width="auto" style={ {"float": "right", "borderRadius":"10%", "margin":"20px 0px 0px 15px"} } alt="me"/>
        <h2>About</h2>
        <p>{state.information.history}</p>
        <h2>Proficiencies</h2>
        <p>{state.information.proficient.join(' ◦ ')}</p>
        <h2>Current</h2>
        <p>{state.information.current}</p>
        <h2>Experience</h2>
        <p>{state.information.experience}</p>
        <h2>Education</h2>
        {state.information.education.map((item, i) =>{
            return <p key={i}>{item}</p>;
            })}
        </div>
        }
        </div>
    )

}

export default About;


