import React, { Component } from 'react';
import {
  BrowserRouter as Router,
  Route
} from 'react-router-dom'
import MuiThemeProvider from 'material-ui/styles/MuiThemeProvider';
import darkBaseTheme from 'material-ui/styles/baseThemes/darkBaseTheme';
import getMuiTheme from 'material-ui/styles/getMuiTheme';
import axios from 'axios';


import MenuPf from './components/Menu';
import Contact from './components/Contact';
import Projects from './components/Projects';
import About from './components/About';

import './App.css';

class App extends Component {
  constructor(){
    super();


    this.state = {
      projLoading: true,
      infoLoading: true,
      information: '',
      projects: [],
    }
  }

  componentDidMount(){
    axios.get('https://gg-portfolio-api.herokuapp.com/information')
    .then(data => this.setState({
        infoLoading: false,
        information: data.data[0],
    }));
    axios.get('https://gg-portfolio-api.herokuapp.com/projects')
    .then(data => this.setState({
        projLoading: false,
        projects: data.data,
    }))
    console.log('If youre checking here 👏! Ill just let you know that these are stateless functional components. When axios resolves its promises, state is updated, and each component will now render the most up to date information from the database.')
  }

  render() {

    return (
      <Router>
        <div>
        <MuiThemeProvider muiTheme={getMuiTheme(darkBaseTheme)}>
                <MenuPf />
      <Route exact path="/" render={() => {
        return <About state={this.state} />
      }} />
      <Route path="/projects" render={() => {
        return <Projects state={this.state} />
      }}/>
      <Route path="/contact" render={() =>{
        return <Contact state={this.state} />
      }}/>
      </MuiThemeProvider>
      </div>
      </Router>
    );
  }
}

export default App;



